#sidebar-overlay {
  background-color: rgba(0, 0, 0, .1);
  bottom: 0;
  display: none;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 5;

  .active {
    display: block;
  }
}

#navtop-btn {
  &:hover {
    cursor: pointer;
  }
}

.sidebar-collapse {
  .sidebar {
    margin-left: -260px;
  }

  .main {
    margin-left: 0;
  }

  @include media-breakpoint-down(md) {
    .sidebar {
      margin-left: 0px;
    }

    #sidebar-overlay {
      display: block;
    }
  }
}