.navtop {
  background: $navtop-bg;
  min-height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  border-bottom-left-radius: $border-radius;
  border-bottom: solid 0.065rem $gray-600;
}

// Dark mode
@include dark-mode () {
  .navtop {
    background: #191C24;
    color: $body-color-alt;

    border-color: $body-border-color-alt;
  }
}