$social-facebook : #365397 !default;
$social-twitter : #00a9f1 !default;
$social-instagram: #396d9a !default;
$social-github : #2f2f2f !default;
$social-google : #4285f4 !default;
$social-youtube : #ff3333 !default;
$social-tiktok : #ee1d51 !default;
$social-whatsapp : #25d366 !default;
$social-telegram : #2aabee !default;
$social-vimeo : #1ab7ea !default;
$social-dribbble : #ea4c89 !default;
$social-pinterest: #bd081c !default;
$social-flickr : #0063dc !default;
$social-bitbucket: #0052cc !default;

$social-colors : ("facebook": $social-facebook,
  "twitter": $social-twitter,
  "instagram": $social-instagram,
  "github": $social-github,
  "google": $social-google,
  "youtube": $social-youtube,
  "tiktok": $social-tiktok,
  "whatsapp": $social-whatsapp,
  "telegram": $social-telegram,
  "vimeo": $social-vimeo,
  "dribbble": $social-dribbble,
  "pinterest": $social-pinterest,
  "flickr": $social-flickr,
  "bitbucket": $social-bitbucket,
) !default;

$white : #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black : #000 !default;

$grays : ("100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900,
) !default;

$blue : #2277c2 !default;
$indigo: #6610f2 !default;
$purple: #686dc3 !default;
$pink : #da1366 !default;
$red : #ad0630 !default;
$orange: #fd7e14 !default;
$yellow: #ffc107 !default;
$green : #058139 !default;
$teal : #528fb3 !default;
$cyan : #39cbfb !default;
$colors: (
  "blue": $blue,
  "indigo": $indigo,
  "purple": $purple,
  "pink": $pink,
  "red": $red,
  "orange": $orange,
  "yellow": $yellow,
  "green": $green,
  "teal": $teal,
  "cyan": $cyan,
  "white": $white,
  "gray": $gray-600,
  "gray-dark": $gray-800,
  ) !default;

$primary : $pink  !default;
$secondary: $gray-800  !default;
$success : $green  !default;
$info : $cyan  !default;
$warning : $yellow  !default;
$danger : $red  !default;
$light : $gray-100  !default;
$dark : $gray-800  !default;
$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark,
  ) !default;

// Body
$body-bg : $gray-100  !default;
$body-color : $gray-900  !default;
