// border-variables
$border-width: 1px !default;
$border-widths: (
  1: 1px,
  2: 2px,
  3: 3px,
  4: 4px,
  5: 5px,
  ) !default;
$border-color : $gray-300  !default;
// border-radius-variables
$border-radius : 0rem !default;
$border-radius-sm : 0rem !default; // .3rem
$border-radius-lg : 0rem !default; // .6rem
$border-radius-pill : 50rem !default;

// box-shadow-variables
$box-shadow : 0 0.5rem 1rem rgba($black, 0.15) !default;
$box-shadow-sm : 0 0.125rem 0.25rem rgba($black, 0.075) !default;
$box-shadow-lg : 0 1rem 3rem rgba($black, 0.175) !default;
$box-shadow-inset : inset 0 1px 2px rgba($black, 0.075) !default;
$component-active-color : $white  !default;
$component-active-bg : $primary  !default;

// caret-variables
$caret-width : 0.3em !default;
$caret-vertical-align : $caret-width * 0.85 !default;
$caret-spacing : $caret-width * 0.85 !default;
$transition-base : all 0.2s ease-in-out !default;
$transition-fade : opacity 0.15s linear !default;

// collapse-transition
$transition-collapse : height 0.35s ease !default;
$transition-collapse-width: width 0.35s ease !default;

// aspect-ratios
$aspect-ratios : ("1x1": 100%,
  "4x3": calc(3 / 4 * 100%),
  "16x9": calc(9 / 16 * 100%),
  "21x9": calc(9 / 21 * 100%),
) !default;

